import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: 'admin',
    lists: [],
  },
  mutations: {
    adduser(state, value) {
      // eslint-disable-next-line no-param-reassign
      this.state.user = value.user;
    },
    addItem(state, value) {
      state.lists.push(value);
    },
  },
  actions: {

  },
});
