<template>
  <div>
    <form class="warpper_login" v-if='!isReg'>
      <h3>欢迎登录</h3>
      <div class="info_box">
        <div class="item"><span class="info">用户名</span><input class="input" type="text" v-model="name"></div>
        <div class="item"><span class="info">密码</span><input class="input" type="password" v-model="password"></div>
      </div>
      <div class="btn_box">
        <div class="btn on" @click="login()">登录</div>
        <div class="btn" @click="reg()">注册</div>
      </div>

    </form>
    <form class="warpper_login" v-else>
      <h3>请输入以下信息</h3>
      <div class="info_box">
        <div class="item"><span class="info">用户名</span><input class="input" type="text" v-model="name"></div>
        <div class="item"><span class="info">密码</span><input class="input" type="password" v-model="password"></div>
        <div class="item"><span class="info">确认密码</span><input class="input" type="password" v-model="repeat"></div>
      </div>
      <div class="btn_box">
        <div class="btn on" @click="addUser()">确定</div>
        <div class="btn" @click="cancel()">取消</div>
      </div>
    </form>
  </div>
</template>

<script>
import store from '@/store';
export default {
  name:'login',
  store,
  data () {
    return {
      isReg: false,
      name: '',
      password: '',
      repeat: ''
    }
  },
  methods: {
    login () {
      if(localStorage.getItem("name")===this.name&&localStorage.getItem("password")===this.password){
        store.commit('adduser',{user: this.name})
        this.$router.push('/home/list')
      }else{
        alert("用户名密码错误")
      }
    },
    reg () {
      this.isReg = true;
    },
    cancel () {
      this.isReg = false;
    },
    addUser () {
      if(this.password&&this.name&&this.repeat&&this.password==this.repeat){
        localStorage.setItem("name", this.name);
        localStorage.setItem("password", this.password);
        this.name='';
        this.password='';
        this.repeat='';
        this.isReg=false;
      }else{
        this.name='';
        this.password='';
        this.repeat='';
        alert("两次密码有误");
      }
    }
  } 
}
</script>

<style lang="scss" scoped>
  .warpper_login{
    width:300px;
    margin: auto;
    padding: 25px 20px;
  }
  .info_box{
    padding: 20px 0;
    .item{
      margin-bottom: 5px; 
    }
  }
  .info{
    width: 70px;
    display: inline-block;
    text-align: right;
  }
  .input{
    margin-left: 5px;
    
  }
</style>